import { Input, Radio, Space } from 'antd';
import { useState } from 'react';
import './question.css';

const data = {
  question_transfer: "Потребуется ли вам трансфер?",
  question_food: "Есть ли у вас особые предпочтения по еде",
  questiond_alco: "Какой алкоголь вы предпочитаете?",
  questions_childrens: "Будет ли с вами на празднике ребенок?"
}

const API_URL = "https://5720o1uc35.execute-api.eu-west-1.amazonaws.com/default/wedding-info-inbound-handler";

const answers = {
  1: "Нет",
  2: "Только до ЗАГСа",
  3: "Только до «Силич»",
  4: "Только после « Силич»",
  5: "До и после «Силич»",
  6: "Нет",
  7: "Не ем мясо",
  8: "Не ем рыбу",
  9: "Вегетарианец",
  10: "Красное вино",
  11: "Белое вино",
  12: "Шампанское",
  13: "Виски/коньяк",
  14: "Водка",
  15: "Не буду пить алкоголь",
  16: "Да",
  17: "Нет"
}

function Questions(props) {
  const [value, setValue] = useState(1);


  const toggleConfirmation = () => {


    const result = props.name + ": " + data.question_transfer + ": " + answers[value_question_transfer] +". "
    + data.question_food + ": " + answers[value_question_food] +". "
    + data.questiond_alco + ": " + answers[value_questiond_alco] +". "
    + data.questions_childrens + ": " + answers[value_questions_childrens] +". ";

    console.log(result);

    fetch(API_URL, {
      method: 'POST',
      mode: 'no-cors',

      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ body: result })
    })
    .then(response => {
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      return response.json();
    })
    .then(data => {
      console.log(data);
    })
    .catch(error => {
      console.error('There was a problem with the fetch operation:', error);
    });


  }

  const [value_question_transfer, setValue_question_transfer] = useState(1);
  const [value_question_food, setValue_question_food] = useState(6);
  const [value_questiond_alco, setValue_questiond_alco] = useState(10);
  const [value_questions_childrens, setValue_questions_childrens] = useState(16);




  const onChange = (e) => {
    const newValue = e.target.value;
    console.log(answers[newValue]);

    if (0 < newValue && newValue < 6) {
      setValue_question_transfer(newValue)
    }

    if (5 < newValue && newValue < 10) {
      setValue_question_food(newValue)
    }

    if (9 < newValue && newValue < 16) {
      setValue_questiond_alco(newValue)
    }

    if (15 < newValue && newValue < 18) {
      setValue_questions_childrens(newValue)
    }


    setValue(newValue);
  };

  return (
    <div className="questions-container">
      <div className='backgroind-img'></div>
      <div className='white-background'>
        <div className='timeline-header'>Ваши пожелания</div>
        <div className='questions-block' style={{ width: '50%' }}>
          <div className='question-label'>{data.question_transfer}</div>
          <Radio.Group onChange={onChange} value={value}>
            <Space direction="vertical">
              <Radio value={1}>Нет</Radio>
              <Radio value={2}>Только до ЗАГСа</Radio>
              <Radio value={3}>Только до «Силич»</Radio>
              <Radio value={4}>Только после « Силич»</Radio>
              <Radio value={5}>До и после «Силич»</Radio>
            </Space>
          </Radio.Group>
          <div className='question-label'>{data.question_food}</div>
          <Radio.Group onChange={onChange} value={value}>
            <Space direction="vertical">
              <Radio value={6}>Нет</Radio>
              <Radio value={7}>Не ем мясо</Radio>
              <Radio value={8}>Не ем рыбу</Radio>
              <Radio value={9}>Вегетарианец</Radio>
            </Space>
          </Radio.Group>
          <div className='question-label'>{data.questiond_alco}</div>
          <Radio.Group onChange={onChange} value={value}>
            <Space direction="vertical">
              <Radio value={10}>Красное вино</Radio>
              <Radio value={11}>Белое вино</Radio>
              <Radio value={12}>Шампанское</Radio>
              <Radio value={13}>Виски/коньяк</Radio>
              <Radio value={14}>Водка</Radio>
              <Radio value={15}>Не буду пить алкоголь</Radio>
            </Space>
          </Radio.Group>
          <div className='question-label'>{data.questions_childrens}</div>
          <Radio.Group onChange={onChange} value={value}>
            <Space direction="vertical">
              <Radio value={16}>Да</Radio>
              <Radio value={17}>Нет</Radio>
            </Space>
          </Radio.Group>
        </div>

        <div className='question-btn' onClick={() => toggleConfirmation()}>Отправить</div>


      </div>


    </div>
  );
}

export default Questions;


